.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.loginForm{
  padding: 50px;
  margin: 0 auto;
  height: 500px;
  width: 500px;
}

.loginFormHeader{
  color: brown;
}

.todoHeader{
  font-size: 50px;
}

.renderTodoList{
  margin: 0 ,auto;
  padding: 10px;
}

.centerAll{
  text-align: center;
}

.addEmployeeForm{
  margin: 0px 10px 10px 10px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



